// tslint:disable:export-name

export * from '@microsoft/sp-build-common';
export * from '@microsoft/gulp-core-build-sass';
// Don't export "serve" from GCB-serve. Export it from sp-build-core-tasks
export { reload, trustDevCert, untrustDevCert } from '@microsoft/gulp-core-build-serve';
export * from '@microsoft/gulp-core-build-webpack';
export * from '@microsoft/gulp-core-build-karma';
export * from '@microsoft/sp-build-core-tasks';

export * from './SPWebBuildRig';
export * from './WebBuildRigConstants';

import { SPWebBuildRig } from './SPWebBuildRig';
import * as Gulp from 'gulp';

export const rig: SPWebBuildRig = new SPWebBuildRig();

// Replace gcb.initialize to call our init stuff first.
export const initialize: (gulp: typeof Gulp) => SPWebBuildRig = (gulp: typeof Gulp): SPWebBuildRig => {
  rig.initialize(gulp);
  return rig;
};
